
#root_div_img
{
    background-color: rgb(205, 203, 211);
}
#profile_img_div
{
    text-align: center;
    width: 50%;
    margin-left: 25%;
    font-size: 1.2em;
    font-style: italic;
}

#choice_img_profile
{
    text-align: center;
    font-size: 1.2em;
    font-style: italic;
    padding: 2%;
}

.img_content
{
    border: 2px solid black;
    display: block;
    border-radius: 50%;
    object-fit: cover;
    margin-left: 19%;
    margin-bottom: 1%;
    margin-top: 5%;
}

.label_div_img_profile
{
    font-weight: bold;
    text-decoration: underline;
    color: blue;
}

.img_profile_inputs
{
    margin-top: 1%;
    margin-bottom: 1%;
    font-size: 0.8em;
    height: 10%;
    box-shadow: 2px 2px 6px;
}

.btn_img_profile
{
    margin-top: 2%;
    margin-right: 2%;
    font-weight: bold;
    font-size: 1em;
    background-color: azure;
    border-radius: 5%;
    cursor: pointer;
    translate: all 0.2 ease;
}