/* ============================================
   404 NOT FOUND - CSS ULTRA-CRATIF
   Thme: Espace + Astronaute perdu
   Style: Animations piques + Glassmorphism
   ============================================ */

/* 
   VARIABLES THME
    */
:root {
  --error-primary: #6366f1;
  --error-secondary: #8b5cf6;
  --error-accent: #ec4899;
  --error-warning: #f59e0b;
  
  /* Space theme */
  --space-dark: #0a0e27;
  --space-deep: #050816;
  --nebula-1: rgba(99, 102, 241, 0.3);
  --nebula-2: rgba(139, 92, 246, 0.3);
  --nebula-3: rgba(236, 72, 153, 0.2);
  
  --text-primary: #f1f5f9;
  --text-secondary: #cbd5e1;
  --text-muted: #94a3b8;
  
  --glow: 0 0 40px rgba(99, 102, 241, 0.6);
  --transition: 400ms cubic-bezier(0.4, 0, 0.2, 1);
}

[data-theme="light"] {
  --space-dark: #e0e7ff;
  --space-deep: #f0f9ff;
  --text-primary: #0f172a;
  --text-secondary: #334155;
  --text-muted: #64748b;
  --glow: 0 0 30px rgba(99, 102, 241, 0.4);
}

/* 
   BASE
    */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.notfound-container {
  min-height: 100vh;
  background: radial-gradient(ellipse at bottom, var(--space-dark) 0%, var(--space-deep) 100%);
  color: var(--text-primary);
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
}

/* 
   CURSOR GLOW
    */
.cursor-glow {
  position: fixed;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: radial-gradient(circle, var(--nebula-1) 0%, transparent 70%);
  pointer-events: none;
  transform: translate(-50%, -50%);
  z-index: 1;
  mix-blend-mode: screen;
  transition: opacity 0.3s;
}

/* 
   STARS
    */
.stars {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 0;
}

.star {
  position: absolute;
  width: 2px;
  height: 2px;
  background: white;
  border-radius: 50%;
  animation: twinkle 3s infinite;
  box-shadow: 0 0 4px rgba(255, 255, 255, 0.8);
}

@keyframes twinkle {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.5); }
}

/* 
   THEME TOGGLE
    */
#theme-toggle-404 {
  position: fixed;
  top: 30px;
  right: 30px;
  z-index: 1000;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.2);
  background: rgba(10, 14, 39, 0.8);
  backdrop-filter: blur(10px);
  cursor: pointer;
  font-size: 1.8rem;
  transition: all var(--transition);
  box-shadow: var(--glow);
}

#theme-toggle-404:hover {
  transform: scale(1.15) rotate(20deg);
  box-shadow: 0 0 60px rgba(99, 102, 241, 0.8);
}

/* 
   CONTENT
    */
.notfound-content {
  position: relative;
  z-index: 10;
  text-align: center;
  padding: 2rem;
  max-width: 900px;
  animation: fadeInUp 1s ease-out;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* 
   ERROR CODE (404)
    */
.error-code {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
  cursor: pointer;
  user-select: none;
}

.digit {
  font-size: clamp(8rem, 20vw, 15rem);
  font-weight: 900;
  background: linear-gradient(135deg, var(--error-primary), var(--error-secondary), var(--error-accent));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 80px rgba(99, 102, 241, 0.5);
  animation: glitch 5s infinite;
  position: relative;
}

.digit-4-1 {
  animation-delay: 0s;
}

.digit-0 {
  position: relative;
  animation-delay: 1s;
}

.digit-4-2 {
  animation-delay: 2s;
}

@keyframes glitch {
  0%, 90%, 100% { transform: translate(0); }
  92% { transform: translate(-5px, 2px); }
  94% { transform: translate(5px, -2px); }
  96% { transform: translate(-3px, 3px); }
  98% { transform: translate(3px, -3px); }
}

/* 
   ORBIT & PLANET (le 0)
    */
.orbit {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 180px;
  height: 180px;
  border: 2px dashed rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: rotate 10s linear infinite;
}

@keyframes rotate {
  from { transform: translate(-50%, -50%) rotate(0deg); }
  to { transform: translate(-50%, -50%) rotate(360deg); }
}

.planet {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2.5rem;
  filter: drop-shadow(0 0 20px rgba(255, 165, 0, 0.8));
  animation: planetBounce 2s ease-in-out infinite;
}

@keyframes planetBounce {
  0%, 100% { transform: translateX(-50%) translateY(0); }
  50% { transform: translateX(-50%) translateY(-10px); }
}

/* 
   TITLES & MESSAGE
    */
.error-title {
  font-size: clamp(2rem, 5vw, 3.5rem);
  font-weight: 900;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #fff, var(--error-primary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: fadeIn 1.5s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.error-message {
  font-size: 1.2rem;
  line-height: 1.8;
  color: var(--text-secondary);
  margin-bottom: 3rem;
  animation: fadeIn 2s ease-out;
}

/* 
   ASTRONAUT
    */
.astronaut {
  position: relative;
  width: 200px;
  height: 300px;
  margin: 3rem auto;
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0) rotate(-5deg); }
  50% { transform: translateY(-30px) rotate(5deg); }
}

.astronaut-body {
  position: relative;
  z-index: 2;
}

/* Helmet */
.helmet {
  position: relative;
  width: 100px;
  height: 100px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.9), rgba(200, 200, 255, 0.8));
  border-radius: 50%;
  margin: 0 auto 10px;
  box-shadow: 
    inset 0 -20px 40px rgba(99, 102, 241, 0.4),
    0 0 40px rgba(99, 102, 241, 0.6);
  border: 4px solid rgba(255, 255, 255, 0.3);
}

.helmet-shine {
  position: absolute;
  top: 15px;
  left: 20px;
  width: 30px;
  height: 30px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  filter: blur(4px);
}

.face {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  animation: spin 3s linear infinite;
}

@keyframes spin {
  0%, 100% { transform: translate(-50%, -50%) rotate(0deg); }
  50% { transform: translate(-50%, -50%) rotate(180deg); }
}

/* Body */
.body {
  position: relative;
  width: 80px;
  height: 100px;
  background: linear-gradient(135deg, #fff, #e0e0ff);
  margin: 0 auto;
  border-radius: 20px 20px 10px 10px;
  box-shadow: 
    inset 0 -10px 20px rgba(99, 102, 241, 0.3),
    0 10px 30px rgba(0, 0, 0, 0.3);
}

/* Arms */
.arm {
  position: absolute;
  width: 15px;
  height: 60px;
  background: linear-gradient(135deg, #fff, #e0e0ff);
  border-radius: 10px;
  top: 10px;
}

.left-arm {
  left: -12px;
  transform: rotate(-30deg);
  animation: waveLeft 2s ease-in-out infinite;
}

.right-arm {
  right: -12px;
  transform: rotate(30deg);
  animation: waveRight 2s ease-in-out infinite;
}

@keyframes waveLeft {
  0%, 100% { transform: rotate(-30deg); }
  50% { transform: rotate(-50deg); }
}

@keyframes waveRight {
  0%, 100% { transform: rotate(30deg); }
  50% { transform: rotate(50deg); }
}

/* Legs */
.legs {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: -5px;
}

.leg {
  width: 20px;
  height: 70px;
  background: linear-gradient(135deg, #fff, #e0e0ff);
  border-radius: 10px 10px 20px 20px;
  box-shadow: inset 0 -5px 10px rgba(99, 102, 241, 0.2);
}

/* Rope */
.rope {
  position: absolute;
  top: -100px;
  left: 50%;
  width: 2px;
  height: 100px;
  background: repeating-linear-gradient(
    0deg,
    rgba(255, 255, 255, 0.8) 0px,
    rgba(255, 255, 255, 0.8) 5px,
    transparent 5px,
    transparent 10px
  );
  transform: translateX(-50%);
  animation: ropeSwing 6s ease-in-out infinite;
}

@keyframes ropeSwing {
  0%, 100% { transform: translateX(-50%) rotate(0deg); }
  50% { transform: translateX(-50%) rotate(5deg); }
}

/* 
   BUTTONS
    */
.action-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  margin: 3rem 0;
  flex-wrap: wrap;
}

.btn-primary-404,
.btn-secondary-404 {
  padding: 1.2rem 2.5rem;
  border-radius: 50px;
  font-size: 1.1rem;
  font-weight: 700;
  text-decoration: none;
  border: none;
  cursor: pointer;
  transition: all var(--transition);
  position: relative;
  overflow: hidden;
}

.btn-primary-404 {
  background: linear-gradient(135deg, var(--error-primary), var(--error-secondary));
  color: white;
  box-shadow: 0 10px 30px rgba(99, 102, 241, 0.4);
}

.btn-primary-404::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.6s;
}

.btn-primary-404:hover::before {
  left: 100%;
}

.btn-primary-404:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 50px rgba(99, 102, 241, 0.6);
}

.btn-secondary-404 {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  color: var(--text-primary);
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.btn-secondary-404:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: var(--error-primary);
  transform: translateY(-5px);
}

/* 
   SUGGESTIONS
    */
.suggestions-section {
  margin-top: 4rem;
}

.suggestions-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: var(--text-secondary);
}

.suggestions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  max-width: 800px;
  margin: 0 auto;
}

.suggestion-card {
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  text-decoration: none;
  color: inherit;
  transition: all var(--transition);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.suggestion-card:hover {
  transform: translateY(-10px);
  background: rgba(99, 102, 241, 0.2);
  border-color: var(--error-primary);
  box-shadow: 0 20px 50px rgba(99, 102, 241, 0.3);
}

.suggestion-icon {
  font-size: 3rem;
  filter: drop-shadow(0 0 20px rgba(99, 102, 241, 0.6));
}

.suggestion-info h3 {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.suggestion-info p {
  font-size: 0.9rem;
  color: var(--text-muted);
}

/* 
   EASTER EGG
    */
.easter-egg-hint {
  margin-top: 2rem;
  min-height: 30px;
}

.hint-text {
  font-size: 0.9rem;
  color: var(--error-accent);
  animation: blink 1s infinite;
}

@keyframes blink {
  0%, 50%, 100% { opacity: 1; }
  25%, 75% { opacity: 0.5; }
}

/* 
   RESPONSIVE
    */
@media (max-width: 768px) {
  .error-code {
    gap: 0.5rem;
  }

  .digit {
    font-size: 6rem;
  }

  .orbit {
    width: 120px;
    height: 120px;
  }

  .planet {
    font-size: 1.8rem;
  }

  .astronaut {
    transform: scale(0.8);
  }

  .action-buttons {
    flex-direction: column;
  }

  .btn-primary-404,
  .btn-secondary-404 {
    width: 100%;
  }

  .suggestions-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .digit {
    font-size: 4rem;
  }

  .error-title {
    font-size: 1.8rem;
  }

  .error-message {
    font-size: 1rem;
  }

  #theme-toggle-404 {
    width: 50px;
    height: 50px;
    top: 20px;
    right: 20px;
  }
}