:root[data-theme="dark"] {
  --hub-bg-main: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
  --hub-bg-card: rgba(30, 30, 35, 0.7);
  --hub-bg-input: rgba(25, 25, 30, 0.8);
  --hub-text-primary: #f0f0f0;
  --hub-text-secondary: #b0b0b5;
  --hub-accent: #ff6b35;
  --hub-accent-hover: #ff8555;
  --hub-accent-glow: rgba(255, 107, 53, 0.3);
  --hub-border: rgba(255, 255, 255, 0.1);
  --hub-success: #4ade80;
  --hub-warning: #fbbf24;
  --hub-purple: #8b5cf6;
  --hub-shadow: rgba(0, 0, 0, 0.3);
}

:root[data-theme="light"] {
  --hub-bg-main: linear-gradient(135deg, #fafafa 0%, #f0e6f6 100%);
  --hub-bg-card: rgba(255, 255, 255, 0.9);
  --hub-bg-input: rgba(250, 250, 252, 0.95);
  --hub-text-primary: #1a1a1a;
  --hub-text-secondary: #666666;
  --hub-accent: #ff6b35;
  --hub-accent-hover: #ff8555;
  --hub-accent-glow: rgba(255, 107, 53, 0.2);
  --hub-border: rgba(0, 0, 0, 0.1);
  --hub-success: #22c55e;
  --hub-warning: #f59e0b;
  --hub-purple: #8b5cf6;
  --hub-shadow: rgba(0, 0, 0, 0.1);
}

.hub-main {
  min-height: 100vh;
  background: var(--hub-bg-main);
  color: var(--hub-text-primary);
  padding: 2rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  transition: background 0.3s, color 0.3s;
  position: relative;
  overflow-x: hidden;
}

.hub-main::before {
  content: '';
  position: fixed;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle at 20% 50%, var(--hub-accent-glow) 0%, transparent 50%),
              radial-gradient(circle at 80% 80%, rgba(139, 92, 246, 0.15) 0%, transparent 50%);
  animation: hubBackgroundFloat 20s ease-in-out infinite;
  pointer-events: none;
  z-index: 0;
}

@keyframes hubBackgroundFloat {
  0%, 100% { transform: translate(0, 0) rotate(0deg); }
  50% { transform: translate(5%, 5%) rotate(5deg); }
}

.hub-main > * {
  position: relative;
  z-index: 1;
}

#hub-theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  border: 2px solid var(--hub-border);
  background: var(--hub-bg-card);
  backdrop-filter: blur(10px);
  color: var(--hub-text-primary);
  font-size: 1.6rem;
  cursor: pointer;
  z-index: 1000;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  box-shadow: 0 4px 15px var(--hub-shadow);
}

#hub-theme-toggle:hover {
  transform: rotate(180deg) scale(1.1);
  border-color: var(--hub-accent);
  box-shadow: 0 0 20px var(--hub-accent-glow);
}

#div_hub_img_profile {
  text-align: center;
  margin-bottom: 3rem;
  padding: 2.5rem;
  background: var(--hub-bg-card);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid var(--hub-border);
  box-shadow: 0 8px 32px var(--hub-shadow);
  animation: hubSlideDown 0.6s ease-out;
}

@keyframes hubSlideDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

#div_hub_img_profile img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  object-fit: cover;
  border: 5px solid var(--hub-accent);
  box-shadow: 0 0 30px var(--hub-accent-glow);
  margin-bottom: 1rem;
  transition: transform 0.3s;
}

#div_hub_img_profile img:hover {
  transform: scale(1.05);
}

#slogan_div {
  margin-top: 1.5rem;
}

.span_slogan {
  font-size: 1.3rem;
  color: var(--hub-text-secondary);
  font-style: italic;
  display: block;
  margin: 0.5rem 0;
  font-weight: 500;
}

.span_slogan strong {
  color: var(--hub-accent);
}

#container_div_hub_skill {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem;
  animation: hubFadeIn 0.8s ease-out 0.2s both;
}

@keyframes hubFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

#div_gauche_hub_sklills,
#div_milieu_hub_skill,
#div_droite_hub_skill {
  background: var(--hub-bg-card);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  padding: 2rem;
  border: 1px solid var(--hub-border);
  box-shadow: 0 8px 32px var(--hub-shadow);
  transition: transform 0.3s, box-shadow 0.3s;
}

#div_gauche_hub_sklills:hover,
#div_milieu_hub_skill:hover,
#div_droite_hub_skill:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px var(--hub-shadow);
}

#show_skills_div {
  margin-bottom: 2rem;
}

#show_skills_div h3 {
  color: var(--hub-accent);
  margin-bottom: 1.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  background: linear-gradient(135deg, var(--hub-accent), var(--hub-purple));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.show_skill_div {
  padding: 1rem;
  background: var(--hub-bg-input);
  border-radius: 10px;
  margin-bottom: 0.75rem;
  border-left: 4px solid var(--hub-accent);
  transition: all 0.3s;
  animation: hubSlideRight 0.4s ease-out;
}

@keyframes hubSlideRight {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.show_skill_div:hover {
  transform: translateX(5px);
  background: rgba(255, 107, 53, 0.1);
  border-left-width: 6px;
}

.h3_hub_skill {
  color: var(--hub-accent);
  font-size: 1.15rem;
  margin: 2rem 0 1.25rem 0;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.input_hub_skill,
textarea.input_hub_skill {
  width: 100%;
  padding: 0.9rem;
  background: var(--hub-bg-input);
  border: 2px solid var(--hub-border);
  border-radius: 10px;
  color: var(--hub-text-primary);
  margin-bottom: 1rem;
  font-family: inherit;
  font-size: 0.95rem;
  transition: all 0.3s;
}

.input_hub_skill:focus,
textarea.input_hub_skill:focus {
  outline: none;
  border-color: var(--hub-accent);
  box-shadow: 0 0 0 3px var(--hub-accent-glow);
  transform: translateY(-2px);
}

textarea.input_hub_skill {
  resize: vertical;
  min-height: 100px;
  line-height: 1.6;
}

#edit_div_hub_skill input[type="file"] {
  margin-bottom: 1rem;
  padding: 0.75rem;
  color: var(--hub-text-primary);
  background: var(--hub-bg-input);
  border: 2px dashed var(--hub-border);
  border-radius: 10px;
  width: 100%;
  cursor: pointer;
  transition: all 0.3s;
}

#edit_div_hub_skill input[type="file"]:hover {
  border-color: var(--hub-accent);
  background: rgba(255, 107, 53, 0.05);
}

#edit_div_hub_skill label,
#portefolio_div_hub_skill label,
#autres_info_div_hub_skill label {
  display: block;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  color: var(--hub-text-secondary);
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.btn_hub_skill_input {
  width: 100%;
  padding: 1rem;
  background: linear-gradient(135deg, var(--hub-accent), var(--hub-accent-hover));
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 700;
  font-size: 1rem;
  transition: all 0.3s;
  margin-bottom: 0.75rem;
  box-shadow: 0 4px 15px var(--hub-accent-glow);
  position: relative;
  overflow: hidden;
}

.btn_hub_skill_input::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.btn_hub_skill_input:hover::before {
  width: 300px;
  height: 300px;
}

.btn_hub_skill_input:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 6px 25px var(--hub-accent-glow);
}

.btn_hub_skill_input:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.btn_hub_skill_input.btn-primary {
  background: linear-gradient(135deg, var(--hub-success), #22d35e);
}

.btn_hub_skill_input.btn-warn {
  background: linear-gradient(135deg, var(--hub-warning), #fb923c);
}

.final-actions {
  margin-top: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.hub-prev-empty {
  text-align: center;
  padding: 3rem;
  color: var(--hub-text-secondary);
  font-size: 1.1rem;
}

.hub-prev-box {
  position: sticky;
  top: 20px;
  animation: hubFadeIn 0.6s ease-out 0.3s both;
}

.hub-prev-title {
  color: var(--hub-accent);
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.hub-prev-section {
  margin-bottom: 2.5rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid var(--hub-border);
}

.hub-prev-section:last-child {
  border-bottom: none;
}

.hub-prev-subtitle {
  color: var(--hub-text-primary);
  margin-bottom: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.hub-prev-text {
  color: var(--hub-text-secondary);
  line-height: 1.8;
  font-size: 0.95rem;
}

.hub-prev-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.hub-skill-tag {
  display: inline-flex;
  align-items: center;
  gap: 0.6rem;
  padding: 0.6rem 1.2rem;
  background: var(--hub-bg-input);
  border-radius: 25px;
  border: 1px solid var(--hub-border);
  font-size: 0.9rem;
  transition: all 0.3s;
}

.hub-skill-tag:hover {
  background: rgba(255, 107, 53, 0.1);
  border-color: var(--hub-accent);
  transform: translateY(-2px);
}

.hub-skill-tag a {
  text-decoration: none;
  font-size: 1.1rem;
  transition: transform 0.2s;
  filter: drop-shadow(0 2px 4px rgba(0,0,0,0.2));
}

.hub-skill-tag a:hover {
  transform: scale(1.3) rotate(10deg);
}

.hub-port-item {
  padding: 1.5rem;
  background: var(--hub-bg-input);
  border-radius: 12px;
  margin-bottom: 1.5rem;
  border-left: 4px solid var(--hub-purple);
  transition: all 0.3s;
}

.hub-port-item:hover {
  transform: translateX(5px);
  box-shadow: 0 4px 20px var(--hub-shadow);
}

.hub-port-name {
  color: var(--hub-purple);
  margin-bottom: 0.75rem;
  font-size: 1.2rem;
  font-weight: 700;
}

.hub-port-desc {
  color: var(--hub-text-secondary);
  font-size: 0.9rem;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.hub-port-links {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.hub-port-link {
  padding: 0.5rem 1rem;
  background: linear-gradient(135deg, var(--hub-accent), var(--hub-accent-hover));
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-size: 0.85rem;
  font-weight: 600;
  transition: all 0.3s;
  box-shadow: 0 2px 10px var(--hub-accent-glow);
}

.hub-port-link:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 15px var(--hub-accent-glow);
}

.hub-helper-box {
  position: sticky;
  top: 20px;
  animation: hubFadeIn 0.6s ease-out 0.4s both;
}

.hub-helper-title {
  color: var(--hub-accent);
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.hub-help-section {
  margin-bottom: 2rem;
  padding: 1.25rem;
  background: var(--hub-bg-input);
  border-radius: 12px;
  border-left: 4px solid var(--hub-accent);
}

.hub-help-subtitle {
  color: var(--hub-text-primary);
  font-size: 1.05rem;
  margin-bottom: 1rem;
  font-weight: 600;
}

.hub-help-list {
  padding-left: 1.5rem;
  color: var(--hub-text-secondary);
  font-size: 0.9rem;
}

.hub-help-list li {
  margin-bottom: 0.6rem;
  line-height: 1.6;
}

.hub-code-example {
  display: block;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  font-size: 0.85rem;
  margin: 1rem 0;
  color: var(--hub-accent);
  font-family: 'Courier New', monospace;
  word-break: break-all;
  border: 1px solid var(--hub-border);
}

.hub-help-text {
  color: var(--hub-text-secondary);
  font-size: 0.9rem;
  margin-bottom: 0.75rem;
  line-height: 1.6;
}

.hub-help-note {
  color: var(--hub-text-secondary);
  font-size: 0.85rem;
  font-style: italic;
  margin-top: 0.5rem;
  opacity: 0.8;
}

.hub-help-tips {
  background: linear-gradient(135deg, rgba(74, 222, 128, 0.1), rgba(34, 197, 94, 0.05));
  border-left-color: var(--hub-success);
}

.hub-help-warning {
  background: linear-gradient(135deg, rgba(251, 191, 36, 0.1), rgba(245, 158, 11, 0.05));
  border-left-color: var(--hub-warning);
}

.hub-not-logged {
  text-align: center;
  padding: 5rem 2rem;
  font-size: 1.3rem;
  animation: hubFadeIn 0.6s ease-out;
}

.hub-not-logged a {
  color: var(--hub-accent);
  text-decoration: none;
  border-bottom: 2px solid var(--hub-accent);
  transition: all 0.3s;
  font-weight: 600;
}

.hub-not-logged a:hover {
  opacity: 0.8;
  border-bottom-width: 3px;
}

@media (max-width: 1200px) {
  #container_div_hub_skill {
    grid-template-columns: 1fr 1fr;
  }
  
  #div_droite_hub_skill {
    grid-column: span 2;
  }
}

@media (max-width: 768px) {
  #container_div_hub_skill {
    grid-template-columns: 1fr;
  }
  
  #div_droite_hub_skill {
    grid-column: span 1;
  }
  
  .hub-main {
    padding: 1rem;
  }

  #div_hub_img_profile {
    padding: 1.5rem;
  }

  #div_hub_img_profile img {
    width: 120px;
    height: 120px;
  }
}