/* ============================================
   GET SALT PAGE - CSS 2026 (scuris & apaisant)
   Style: Cyber-minimal + Glassmorphism + confiance
   ============================================ */

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=JetBrains+Mono:wght@400;700&display=swap');

:root {
  --salt-bg: linear-gradient(135deg, #0a0e1a 0%, #111827 100%);
  --salt-glass: rgba(17, 24, 39, 0.45);
  --salt-glass-strong: rgba(31, 41, 55, 0.65);
  --salt-accent: #22d3ee;         /* cyan lectrique scuris */
  --salt-accent-soft: #67e8f9;
  --salt-accent-glow: rgba(34, 211, 238, 0.25);
  --salt-text: #e5e7eb;
  --salt-text-muted: #9ca3af;
  --salt-border: rgba(34, 211, 238, 0.2);
  --salt-shadow: 0 10px 40px rgba(0, 0, 0, 0.6);
  --salt-shadow-glow: 0 0 30px rgba(34, 211, 238, 0.2);
  --blur: 16px;
  --transition: 0.35s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Reset & base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#div_root_getsalt {
  min-height: 100vh;
  background: var(--salt-bg);
  color: var(--salt-text);
  font-family: 'Inter', system-ui, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  overflow: hidden;
  position: relative;
}

/* Fond subtil cyber */
#div_root_getsalt::before {
  content: '';
  position: absolute;
  inset: 0;
  background: 
    radial-gradient(circle at 20% 30%, rgba(34, 211, 238, 0.08) 0%, transparent 40%),
    radial-gradient(circle at 80% 70%, rgba(34, 211, 238, 0.06) 0%, transparent 50%);
  pointer-events: none;
  z-index: 0;
}

/* Titre explication */
#h3_get_salt {
  font-family: 'JetBrains Mono', monospace;
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--salt-accent-soft);
  text-align: center;
  margin-bottom: 24px;
  letter-spacing: 1px;
  text-shadow: 0 0 15px rgba(34, 211, 238, 0.4);
  position: relative;
  z-index: 1;
}

/* Bloc explication */
#div_explain_salt {
  max-width: 720px;
  background: var(--salt-glass);
  backdrop-filter: blur(var(--blur));
  -webkit-backdrop-filter: blur(var(--blur));
  border: 1px solid var(--salt-border);
  border-radius: 16px;
  padding: 32px 40px;
  margin-bottom: 40px;
  box-shadow: var(--salt-shadow);
  position: relative;
  z-index: 1;
}

#explain_salt {
  font-size: 1.1rem;
  line-height: 1.7;
  color: var(--salt-text-muted);
  text-align: center;
}

#explain_salt strong {
  color: var(--salt-accent);
  font-weight: 600;
}

/* Bouton obtenir salt */
#getsalt_btn_div {
  margin: 32px 0;
  text-align: center;
  position: relative;
  z-index: 1;
}

#getsalt_btn {
  padding: 16px 48px;
  background: linear-gradient(135deg, var(--salt-accent), #0ea5e9);
  color: white;
  border: none;
  border-radius: 12px;
  font-family: 'JetBrains Mono', monospace;
  font-size: 1.15rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  box-shadow: 0 8px 32px rgba(34, 211, 238, 0.35);
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
}

#getsalt_btn:hover {
  transform: translateY(-4px);
  box-shadow: 0 16px 48px rgba(34, 211, 238, 0.55);
}

#getsalt_btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  animation: pulseLoading 1.8s infinite;
}

@keyframes pulseLoading {
  0%, 100% { box-shadow: 0 0 0 0 rgba(34, 211, 238, 0.4); }
  50%      { box-shadow: 0 0 0 20px rgba(34, 211, 238, 0); }
}

#getsalt_btn::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: rgba(255, 255, 255, 0.25);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

#getsalt_btn:hover::before {
  width: 300px;
  height: 300px;
}

/* Rsultat affich */
#global_div_getsalt {
  max-width: 720px;
  background: var(--salt-glass-strong);
  backdrop-filter: blur(12px);
  border: 1px solid var(--salt-border);
  border-radius: 16px;
  padding: 32px;
  margin-top: 32px;
  box-shadow: var(--salt-shadow-glow);
  position: relative;
  z-index: 1;
}

.div_part_getsalt {
  display: flex;
  flex-direction: column;
  margin-bottom: 24px;
  padding: 16px;
  background: rgba(17, 24, 39, 0.5);
  border-radius: 12px;
  border: 1px solid var(--salt-border);
}

.label_getsalt {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--salt-accent-soft);
  margin-bottom: 8px;
}

.span_getsalt {
  font-family: 'JetBrains Mono', monospace;
  font-size: 1.1rem;
  color: var(--salt-text);
  word-break: break-all;
  background: rgba(0, 0, 0, 0.3);
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px dashed var(--salt-border);
}

/* Erreur */
#err_div_getsalt {
  max-width: 720px;
  background: rgba(239, 68, 68, 0.15);
  border: 1px solid rgba(239, 68, 68, 0.4);
  border-radius: 16px;
  padding: 24px 32px;
  margin-top: 32px;
  color: #fecaca;
  text-align: center;
  box-shadow: 0 8px 32px rgba(239, 68, 68, 0.2);
  position: relative;
  z-index: 1;
}

#strong_err_getsalt {
  color: #fca5a5;
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 12px;
  display: block;
}

#p_err_getsalt {
  font-size: 1.05rem;
  line-height: 1.6;
}

/* Responsive */
@media (max-width: 768px) {
  #div_root_getsalt {
    padding: 24px 16px;
  }
  #h3_get_salt {
    font-size: 1.6rem;
  }
  #div_explain_salt {
    padding: 24px 28px;
  }
  #getsalt_btn {
    padding: 14px 40px;
    font-size: 1.05rem;
  }
  .div_part_getsalt {
    padding: 12px 16px;
  }
  .span_getsalt {
    font-size: 1rem;
  }
}

/* Animation fade-in rsultat */
#global_div_getsalt,
#err_div_getsalt {
  animation: fadeIn 0.6s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to   { opacity: 1; transform: translateY(0); }
}