/* ============================================
   NEXUS-FOUR - Navigation CSS FINAL
   Sans conflits, z-index optimis
   ============================================ */

/* ===== VARIABLES THME ===== */
:root {
  --nav-bg: rgba(10, 14, 39, 0.95);
  --nav-border: rgba(0, 229, 255, 0.2);
  --nav-link: #9fa8da;
  --nav-link-hover: #00e5ff;
  --nav-active: #00e5ff;
  --nav-shadow: rgba(0, 229, 255, 0.3);
  --nav-gradient-start: rgba(0, 229, 255, 0.1);
  --nav-gradient-end: rgba(26, 31, 58, 0.1);
}

/* ===== NAVIGATION PRINCIPALE ===== */
nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999; /*  999 pour nav, boutons thme 10000 */
  background: var(--nav-bg);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 2px solid var(--nav-border);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  padding: 0;
  margin: 0;
}

nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  list-style: none;
  margin: 0;
  padding: 12px 20px; /*  Rduit de 15px  12px pour hauteur */
  max-width: 1800px;
  margin: 0 auto;
}

nav li {
  position: relative;
  margin: 0;
}

/* ===== LIENS NAVIGATION ===== */
nav a {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 18px; /*  Rduit padding vertical */
  color: var(--nav-link);
  text-decoration: none;
  font-size: 14px;
  font-weight: 600;
  border-radius: 12px;
  background: linear-gradient(135deg, var(--nav-gradient-start), var(--nav-gradient-end));
  border: 1px solid transparent;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}

/* Effet de brillance au survol */
nav a::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 229, 255, 0.2), transparent);
  transition: left 0.5s ease;
  pointer-events: none;
}

nav a:hover::before {
  left: 100%;
}

/* Hover state */
nav a:hover {
  color: var(--nav-link-hover);
  background: linear-gradient(135deg, rgba(0, 229, 255, 0.15), rgba(0, 229, 255, 0.05));
  border-color: var(--nav-border);
  transform: translateY(-2px);
  box-shadow: 0 4px 20px var(--nav-shadow);
}

/* Active state */
nav a.active,
nav a:active {
  color: white;
  background: linear-gradient(135deg, var(--nav-active), rgba(0, 229, 255, 0.7));
  border-color: var(--nav-active);
  box-shadow: 0 4px 20px var(--nav-shadow), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

/* Underline animation */
nav a::after {
  content: '';
  position: absolute;
  bottom: 2px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background: var(--nav-active);
  transition: width 0.3s ease;
}

nav a:hover::after {
  width: 70%;
}

/* ===== COULEURS PAR SECTION ===== */
nav a[href="/"] {
  background: linear-gradient(135deg, rgba(255, 193, 7, 0.12), rgba(255, 193, 7, 0.04));
}

nav a[href="/"]:hover {
  border-color: rgba(255, 193, 7, 0.3);
  box-shadow: 0 4px 20px rgba(255, 193, 7, 0.3);
}

nav a[href="/chat_nexus"] {
  background: linear-gradient(135deg, rgba(33, 150, 243, 0.12), rgba(33, 150, 243, 0.04));
}

nav a[href="/chat_nexus"]:hover {
  border-color: rgba(33, 150, 243, 0.3);
  box-shadow: 0 4px 20px rgba(33, 150, 243, 0.3);
}

nav a[href="/care"] {
  background: linear-gradient(135deg, rgba(76, 175, 80, 0.12), rgba(76, 175, 80, 0.04));
}

nav a[href="/care"]:hover {
  border-color: rgba(76, 175, 80, 0.3);
  box-shadow: 0 4px 20px rgba(76, 175, 80, 0.3);
}

nav a[href="/hub"] {
  background: linear-gradient(135deg, rgba(255, 107, 53, 0.12), rgba(255, 107, 53, 0.04));
}

nav a[href="/hub"]:hover {
  border-color: rgba(255, 107, 53, 0.3);
  box-shadow: 0 4px 20px rgba(255, 107, 53, 0.3);
}

nav a[href="/marketplace"] {
  background: linear-gradient(135deg, rgba(156, 39, 176, 0.12), rgba(156, 39, 176, 0.04));
}

nav a[href="/marketplace"]:hover {
  border-color: rgba(156, 39, 176, 0.3);
  box-shadow: 0 4px 20px rgba(156, 39, 176, 0.3);
}

nav a[href="/ws"] {
  background: linear-gradient(135deg, rgba(0, 229, 255, 0.12), rgba(0, 229, 255, 0.04));
}

/* ===== MOBILE HAMBURGER ===== */
.nav-mobile-toggle {
  display: none;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: var(--nav-link-hover);
  font-size: 28px;
  cursor: pointer;
  padding: 8px;
  z-index: 1001; /* Au-dessus nav ul */
  transition: transform 0.3s ease;
}

.nav-mobile-toggle:hover {
  transform: translateY(-50%) scale(1.1);
}

/* ===== RESPONSIVE ===== */
@media (max-width: 1400px) {
  nav ul {
    gap: 8px;
  }
  
  nav a {
    padding: 9px 15px;
    font-size: 13px;
  }
}

@media (max-width: 1200px) {
  nav ul {
    gap: 6px;
  }
  
  nav a {
    padding: 8px 14px;
    font-size: 13px;
  }
}

@media (max-width: 900px) {
  .nav-mobile-toggle {
    display: block;
  }
  
  nav {
    padding: 10px 0;
  }
  
  nav ul {
    position: fixed;
    top: 60px; /*  Rduit pour nav plus compacte */
    left: 0;
    right: 0;
    flex-direction: column;
    background: var(--nav-bg);
    backdrop-filter: blur(20px);
    padding: 20px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.4s ease;
    border-bottom: 2px solid var(--nav-border);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
    z-index: 1000;
  }
  
  nav ul.open {
    max-height: 90vh; /*  Limite hauteur pour viter dbordement */
    overflow-y: auto; /*  Scroll si trop de liens */
  }
  
  nav li {
    width: 100%;
  }
  
  nav a {
    width: 100%;
    justify-content: center;
    padding: 14px;
    font-size: 15px;
  }
}

@media (max-width: 600px) {
  nav a {
    font-size: 14px;
    padding: 12px;
  }
}

/* ===== ANIMATIONS ===== */
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

nav {
  animation: slideDown 0.5s ease;
}

nav li {
  animation: slideDown 0.5s ease;
  animation-fill-mode: backwards;
}

nav li:nth-child(1) { animation-delay: 0.05s; }
nav li:nth-child(2) { animation-delay: 0.1s; }
nav li:nth-child(3) { animation-delay: 0.15s; }
nav li:nth-child(4) { animation-delay: 0.2s; }
nav li:nth-child(5) { animation-delay: 0.25s; }
nav li:nth-child(6) { animation-delay: 0.3s; }
nav li:nth-child(7) { animation-delay: 0.35s; }
nav li:nth-child(8) { animation-delay: 0.4s; }
nav li:nth-child(9) { animation-delay: 0.45s; }
nav li:nth-child(10) { animation-delay: 0.5s; }

/* ===== ESPACEMENT CONTENU ===== */
body {
  margin: 0;
  padding: 0;
}

.content-wrapper,
body > div:not(nav) {
  padding-top: 70px; /*  Nav compacte = moins d'espace */
}

/* ===== SCROLLBAR NAV MOBILE ===== */
nav ul::-webkit-scrollbar {
  width: 6px;
}

nav ul::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
}

nav ul::-webkit-scrollbar-thumb {
  background: var(--nav-active);
  border-radius: 3px;
}

nav ul::-webkit-scrollbar-thumb:hover {
  background: var(--nav-link-hover);
}

/* ===== FIX CONFLIT AVEC AUTRES LMENTS ===== */
/* Tout ce qui doit tre au-dessus de la nav */
.theme-toggle,
#hub-theme-toggle,
#care-theme-toggle,
.modal,
.popup,
.tooltip {
  z-index: 10000 !important; /* Au-dessus nav (999) */
}

/* ===== SMOOTH SCROLL POUR ANCRES ===== */
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px; /* Espace pour nav fixe */
}

/* ===== EFFET BLUR SUR SCROLL (optionnel) ===== */
nav.scrolled {
  background: rgba(10, 14, 39, 0.98);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}

/* ===== PRINT (cache nav  l'impression) ===== */
@media print {
  nav {
    display: none;
  }
  
  body {
    padding-top: 0;
  }
}